@import "variables";
.page-about-us {
  .moc-inner {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 30px;
    width: 100%;
    box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
  }

  h1, h2 {
    color: #333333;
  }

  h2 {
    font-size: 48px;
    font-family: $roboto;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    font-weight: 300;
  }

  .grey-section {
    background-color: $grey_section_bg;
  }

  .back-bg {
    position: absolute;
    z-index: -2;
    height: 100vh;
    width: 100%;
    max-height: 920px;
  }

  #bot-form__wrapper {
    margin-bottom: 100px;
  }

  .owl-carousel {
    .owl-dot {
      width: 0px;
      height: 0px;
      border: 4px solid #7d7d7d !important;

      &.active {
        border: 4px solid #000000 !important;
      }
    }
  }
}

@media (min-width: 768px) and (max-width: 1144px) {
  .page-about-us {
    h1 {
      font-size: 48px !important;
    }

    h2 {
      font-size: 40px;
    }

    p {
      font-size: 16px;
    }

    .section-description {
      width: unset !important;
    }
  }
}

@media all and (max-width: 767px) {
  .page-about-us {
    .moc-inner {
      padding: 0 20px !important;
    }

    h1 {
      font-size: 48px !important;
      line-height: 1.13 !important;
      letter-spacing: -0.75px !important;
    }

    h2 {
      font-size: 32px;
    }

    p {
      font-size: 16px !important;
    }

    .section-description {
      width: unset !important;
    }

    .our-purpose .section-description {
      padding: unset !important;
    }
  }
}





.awards-recognitions {
    padding-top: 72px;
    padding-bottom: 150px;

    @media all and (max-width: 1024px) {
        padding-top: 91px;
        padding-bottom: 78px;
    }

    @media all and (max-width: 680px) {
        padding-top: 80px;
        padding-bottom: 54px;
    }

    h2 {
        margin-top: 0;
        margin-bottom: 60px;

        @media all and (max-width: 680px) {
            margin-bottom: 10px;
        }
    }

    .live-person-title {
        margin-bottom: 45px;

        @media all and (max-width: 1024px) {
            margin-bottom: 20px;
        }
    }

    .awards-logos {
        display: flex;
        justify-content: space-between;
        margin-bottom: 150px;
        margin-right: -15px;
        margin-left: -15px;


        @media all and (max-width: 1024px) {
            margin-bottom: 58px;
            margin-right: auto;
            margin-left: auto;
            max-width: 600px;
            flex-wrap: wrap;
            justify-content: center;
        }

        @media all and (max-width: 414px) {
            width: 100%;
            margin-bottom: 40px;
        }

        .awards-logos-item {
            flex-basis: 264px;
            padding: 16px 9px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #ffffff;
            margin: 0 15px;
            box-sizing: border-box;
            min-height: 100px;
            position: relative;

            @media all and (max-width: 1024px) {
                margin-bottom: 30px;
                min-height: 82px;
            }

            @media all and (max-width: 680px) {
                max-width: unset;
                flex-basis: unset;
                min-height: 65px;
                padding: 9px;
                margin-bottom: 30px;
            }

            @media all and (max-width: 414px) {
                margin: 0 0 40px;

            }

            &:hover {
                border: 1px solid $brand_color;
                cursor: pointer;
            }
        }
    }

    .awards-logos-link {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .awards-logo-image {
        max-width: 100%;
    }

    .capterra-logo {
        @media all and (max-width: 680px) {
            max-width: 149px;
        }
    }

    .clutch-image {
        @media all and (max-width: 680px) {
            max-width: 91px;
        }
    }

    .lp-image-wrapper {
        max-width: 770px;
        margin: 0 auto;
    }

    .lp-image {
        width: 100%;
        max-width: 100%;
        vertical-align: middle;
    }
}

@media all and (max-width: 680px) {
    .awards-recognitions {
        h2 {
            margin-top: 0;
            margin-bottom: 54px;
        }
        .awards-logos {
            flex-direction: column;
            align-items: center;

            div {
                width: 100%;
                min-width: 236px;

                img {
                    max-height: 42px;
                    width: auto;
                }
            }
        }
    }
}
.page-about-us {
    .company-facts {
        margin-bottom: 110px;

        h2 {
            margin: 100px auto 56px auto;
        }

        .col-3 {
            width: 31.5%;
            height: 158px;
            p {
                padding: 36px 32px 2px 32px;
            }
            span {
                padding: 0px 16px 36px 32px;
            }
        }
        .col-4 {
            width: 23%;
            min-height: 116px;
            p {
                padding: 16px 32px 0px 32px;
            }
            span {
                padding: 0px 16px 16px 32px;
            }
        }

        #company-facts {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: flex-start;
            height: 300px;
        }
        .facts-item {
            box-shadow: 0 2px 10px 0 rgba(177, 177, 177, 0.5);
            margin-bottom: 24px;

            p {
                font-size: 18px;
                font-weight: normal;
                font-stretch: normal;
                font-style: normal;
                line-height: 1.78;
                letter-spacing: normal;
                color: #6c6c6c;
            }
            span {
                font-size: 44px;
                font-weight: 900;
                font-stretch: normal;
                font-style: normal;
                line-height: 1.18;
                letter-spacing: normal;
                color: #f1563c;
            }
            .small-facts-text {
                display: flex;
                align-items: center;
                padding: 0 12px 16px 0;

                span {
                    font-weight: 900 !important;
                    font-size: 10px;
                    font-stretch: normal;
                    font-style: normal;
                    line-height: 1.2;
                    letter-spacing: normal;
                    color: #f1563c;

                    padding: 0 !important;
                }
                p {
                    padding: 0 !important;
                    font-size: 10px;
                    font-weight: normal;
                    color: #f1563c;
                    line-height: 1;
                }        
            
            }
            & > div {
                display: flex;
            }
        }
    }
}

